export default {
  data () {
    return {
      actionShow: true
    }
  },
  methods: {
    Add () {
      this.$emit('Add')
    },
    Delete () {
      this.actionShow = true
      this.$emit('Delete')
    },
    preDelete () {
      this.actionShow = false
      this.$emit('preDelete')
    },
    backPre () {
      this.actionShow = true
      this.$emit('backPre')
    }
  }
}



